import { apiHelper } from "./../utils/helpers";

export default {
  // 
  signIn({ email, password }) {
    //  return  Promise
    return apiHelper.post("/login", {
      email,
      password,
    });
  },
  signUp(data) {
    return apiHelper.post("/users", { ...data });
  },
  adminLogin({ email, password }) {
    return apiHelper.post("/adminLogin", {
      email,
      password,
    })
  },
};
